@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;800&display=swap);
:root {
    --dark: rgb(29, 33, 37);
    --dark-secondary: #334e68;
    --dark-text: #8c9095;
    --light-text: #f0f4f8;
    --dark-green: rgb(36, 158, 115);
    --light-green: rgb(133, 221, 183);
    --blue: rgb(17, 190, 221);
    --light-purple: #e1c7e0;
    --dark-purple: #c054be;
}

body {
    background-color: rgb(29, 33, 37);
    background-color: var(--dark);
    font-family: "Lato";
    margin: 0;
    overflow-x: hidden;
    -ms-scroll-chaining: none;
        overscroll-behavior: none;
}

.App-header {
    /* font-size: 20px; */
    color: #f0f4f8;
    color: var(--light-text);
    padding: 36px;
    padding-bottom: 12px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.App-select {
    padding: 8px;
    width: 20%;
}

.App-escape {
    padding: 8px;
    text-align: right;
    color: #8c9095;
    color: var(--dark-text);
}

.App-data {
    padding: 8px;
    text-align: left;
    color: #8c9095;
    color: var(--dark-text);
}

.App-step {
    color: white;
}

.App-step-text-center {
    text-align: center;
}

::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

 ::-webkit-scrollbar-thumb {
    background: #fff1;
    border-radius: 4px;
}

 ::-webkit-scrollbar-track {
    background: none;
}
.Bubbles-lyricRow {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 12px;
}

.Bubbles-ogLyric,
.Bubbles-kbLyric {
    padding: 12px;
    margin-top: 4px;
    background-color: #456381;
    border-radius: 16px;
    color: var(--light-text);
}

.Bubbles-kbLyric {
    text-align: right;
}

.Bubbles-arrow {
    -webkit-align-self: center;
            align-self: center;
    color: var(--dark-text);
    min-width: 40px;
    text-align: center;
}

.Bubbles-layer {
    font-weight: 200;
    text-align: left;
    margin-top: 0;
    padding-left: 12px;
    color: var(--light-text);
    width: 300px;
}

b {
    font-weight: 200;
}

button {
    border: none;
    background-color: transparent;
    font-family: "Lato";
    color: var(--light-text);
    cursor: pointer;
}

.ogLyric-bad {
    background-color: var(--dark-purple);
    padding: 0px 4px;
    border-radius: 2px;
}

.alcohol .ogLyric-bad {
    background-color: #00875A;
}

.identity .ogLyric-bad {
    background-color: #0a8ba2;
}

.sexual .ogLyric-bad {
    background-color: #C054BE;
}

.profanity .ogLyric-bad {
    background-color: #d08b00;
}

.other .ogLyric-bad {
    background-color: #596066;
}

.violence .ogLyric-bad {
    background-color: #cb3d1d;
}

.kblyric-different {
    background-color: var(--light-text);
    padding-left: 4px;
    border-radius: 2px;
    color: var(--dark)
}

.kblyric-same.start {
    padding-left: 2px;
}
.IntroStep {
    -webkit-animation: fadeIn linear 2s;
            animation: fadeIn linear 2s;
    text-align: left;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: auto 15vw;
    -webkit-justify-content: center;
            justify-content: center;
}

.header {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 24px;
}

.desc {
    font-size: 24px;
}

.notice {
    margin-top: 1em;
    font-style: italic;
    opacity: 50%;
}

.center-image {
    margin: auto;
    display: block;
    padding: 16px;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
}

.Step2 {
    margin: 10vh 0;
}

.Step2-p {
    font-size: 1.5em;
    margin: 0 15vw 24px 15vh;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.launch-button {
    background-color: rgb(255, 255, 255);
    color: black;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 24px;
    transition: all 0.2s ease-in-out;
    border: 4px solid gray;
    text-decoration: none;
    margin-top: 24px;
}

.launch-button:hover {
    color: rgb(255, 255, 255);
    background-color: black;
    cursor: pointer;
}
.page-container {
    color: var(--light-text);
    padding: 36px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
}

.title {
    text-align: left;
    margin-left: 5vw;
    margin-top: 0;
}

.timeline-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex: 1 1;
            flex: 1 1;
    min-height: 0;
}

.legend-container {
    padding: 0 24px;
}

.timeline-rect:hover {
    cursor: pointer;
}

.timeline-popup-box {
    border-radius: 15px;
