}

.timeline-popup-header {
    text-align: center;
    margin-bottom: 12px;
}

.timeline-popup-title {
    position: -webkit-sticky;
    position: sticky;
    z-index: 12;
    margin: 0;
}

.timeline-popup-category {
    padding: 8px;
    margin: 0;
}

.circle {
    width: 12px;
    max-width: 12px;
    max-height: 12px;
    min-width: 12px;
    min-height: 12px;
    height: 12px;
    border-radius: 12px;
    margin: 0px 12px;
}

.Timeline-legend {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-right: 24px;
}

.Timeline-legend-item {
