.timeline-popup-subtitle {
    color: var(--light-text);
    background-color: var(--dark-secondary);
    border-bottom: 2px solid #fff6;
    text-align: center;
    margin: 0;
    padding-bottom: 8px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 18;
}

.timeline-popup-left {
    overflow: auto;
    width: 30%
}

.timeline-popup-left-item {
    border-radius: 8px;
    margin: 8px;
    padding: 8px;
    transition: all 0.2s ease-in-out;
    color: var(--dark);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.timeline-popup-left-item:hover {
    cursor: pointer;
}

.timeline-popup-right {
    overflow: auto;
    margin-left: 24px;
    width: 100%;
}

.timeline-popup-right-song-section {
    margin-bottom: 16px;
}

.timeline-popup-right-song-title {
    padding: 8px;
    font-weight: bold;
    width: calc(100% - 16px);
    background-color: var(--dark-purple);
    color: white;
    position: -webkit-sticky;
    position: sticky;
    top: 26px;
    z-index: 15;
}

.timeline-popup-right-lyric-section {
    margin: 0 16px;
}

.timeline-popup-right-lyric-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    color: var(--light-text);
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 12px 0px;
}

.timeline-popup-right-lyric-item:not(:last-child) {
    /* border-bottom: 1px solid gray; */
}

#timeline-popup-button {
    font-size: 1.5em;
    z-index: 13;
}

.profanity-container {
    text-align: center;
    margin: 12px auto;
}

.timeline-profanity-ack {
    position: relative;
    bottom: 1em;
