}

.single-year {
    font-size: 16px;
    padding: 4px;
    color: #797979;
    transition: color 200ms ease-in-out;
    white-space: nowrap;
}

.single-year:hover,
.single-year.selected {
    color: #e0e0e0;
    cursor: pointer;
    text-shadow: 0px 0px 20px #e0e0e0;
}

.divider {
    width: 100%;
