.About-launch-button {
    background-color: rgb(255, 255, 255);
    color: black;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 24px;
    transition: all 0.2s ease-in-out;
    border: 4px solid gray;
